.bg-center {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

// utilities 通用類別
.d-flex {
    display: flex;
}

.flex-column {
    flex-direction: column;
}

.flex-wrap {
    flex-wrap: wrap;
}

.jc-center {
    justify-content: center;
}

.jc-star {
    justify-content: flex-start;
}

.jc-end {
    justify-content: flex-end;
}

.jc-around {
    justify-content: space-around;
}

.jc-between {
    justify-content: space-between;
}

.align-star {
    align-items: flex-start;
}

.align-center {
    align-items: center;
}

.align-end {
    align-items: flex-end;
}

.h-100 {
    height: 100%;
}

.mr-1 {
    margin-right: 0.5rem;
}

.mb-1 {
    margin-bottom: 0.5rem;
}

.mb-2 {
    margin-bottom: 1rem;
}

.border-t {
    border-top: 1px solid #000 !important;
}

.sub-title {
    font-size: 3rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 3.75rem;

    @include pad {
        font-size: 2rem;
        margin-bottom: 2rem;
    }

    @include mobile {
        font-size: 1.5rem;
        margin-bottom: 1rem;
    }
}

.text-right {
    text-align: right;
}

.text-center {
    text-align: center;
}

.text-white {
    color: $white;
}

.text-p-dark {
    color: $primary-dark;
}


// 換圖片設定
.sm-img {
    @include pad {
        display: none;
    }
}

.md-img {
    display: none;

    @include pad {
        display: block;
    }
}

//pad
@include pad {
    .jc-md-center {
        justify-content: center;
    }

    .order-md-1 {
        order: 1;
    }

    .order-md-2 {
        order: 2;
    }

}


// mobile
@include mobile {
    .d-sm-none {
        display: block;
    }

    .jc-sm-center {
        justify-content: center;
    }

    .mb-sm-1 {
        margin-bottom: 0.5rem;
    }
}
